.contacts {
  // border: 2px solid;
  // padding: 5px 0 10px;
  // border-left: none;
  // border-right: none;

  &__title {
    font-size: 20px;
    line-height: 170%;
    /* or 34px */
    letter-spacing: 0.06em;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 20px;
    border-top: 1px solid $basic_white;
    padding-top: 30px;

    @media (min-width: $tablet-width) {
      text-align: center;
      font-size: 30px;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      max-width: 86px;
      background-color: $basic_white;
      height: 2px;

      @media (min-width: $tablet-width) {
        margin: 0 auto;
      }
    }
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 100px 1fr;
    margin-bottom: 10px;

    @media (min-width: $tablet-width) {
      grid-template-columns: 1fr;
      text-align: center;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 170%;
    // margin-bottom: 10px;
    @media (min-width: $tablet-width) {
      font-size: 20px;
    }
  }

  &__phone[href^="tel:"] {
    // font-family: "Forum", cursive;
    // font-weight: 400;
    margin: 0;
    padding: 0;
    color: $basic_white;

    &:hover {
      color: $basic_black;
    }
  }
}
