.catalog {
  margin-bottom: 100px;

  &__title {
    font-size: 20px;
    line-height: 170%;
    /* identical to box height, or 34px */
    letter-spacing: 0.06em;
    text-transform: uppercase;
    margin-bottom: 20px;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 35px;
      left: 0;
      right: 0;
      // margin: 0 auto;
      border-bottom: 2px solid $basic_white;
      max-width: 86px;
    }

    @media (min-width: $tablet-width) {
      font-size: 30px;
      line-height: 33px;
      text-align: center;
      margin-bottom: 52px;

      &::after {
        // left: 42%;
        top: 55px;
        margin: 0 auto;
      }
    }

    &-shadow {
      display: none;

      @media (min-width: $desktop-width) {
        display: block;
        margin: -65px auto -110px;
        font-size: 220px;
        line-height: 243px;
        text-align: center;
        letter-spacing: 0.06em;
        text-transform: uppercase;
        color: #ffffff;
        opacity: 0.03;
      }
    }
  }

  &__slider {
    max-width: 290px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      max-width: 767px;
    }

    @media (min-width: $desktop-width) {
      max-width: 1900px;

      // .slick-track {
      //   width: 1550px !important;
      // }

      // .slick-slide {
      //   width: 350px !important;
      // }
    }
  }

  &__item {
    display: flex !important;
    justify-content: center;
    position: relative;

    @media (min-width: $tablet-width) {
      :hover {
        .catalog__item-info {
          opacity: 1;
          cursor: pointer;
          transition: all 0.9s;
        }

        .catalog__item-text {
          bottom: 140px;
          z-index: 15;
          transition: all 0.9s;
        }
      }
    }

    @media (min-width: $desktop-width) {
      display: flex !important;
    }

    &-box {
      position: relative;

      img {
        width: 330px;
      }
    }

    &-number {
      font-family: "Forum", cursive;
      font-weight: 400;
      color: $basic_white;
      font-size: 20px;
      line-height: 22px;
      position: absolute;
      top: 26px;
      left: 100px;

      &::after {
        content: "";
        position: absolute;
        bottom: 11px;
        left: -80px;
        border-bottom: 1px solid #ffffff;
        width: 70px;
      }
    }

    &-text {
      bottom: 67px;
      position: relative;
      padding-left: 20px;
      font-weight: 400;
      font-size: 16px;
      line-height: 170%;
      /* or 27px */
      letter-spacing: 0.1em;
      text-transform: uppercase;
      color: $basic_white;
      opacity: 0.9;
    }

    &-info {
      display: none;
    }

    @media (min-width: $tablet-width) {
      &-info {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(0, 0, 0, 0.6);
        border: 1px solid rgba(255, 255, 255, 0.2);
        max-height: 450px;
        // width: 350px;
        opacity: 0;
      }

      &-button__view {
        background: $basic_white;
        // border: 1px solid $basic_white;
        font-size: 12px;
        line-height: 13px;
        text-align: center;
        letter-spacing: 0.15em;
        text-transform: uppercase;
        padding: 18px 30px;
        color: #010001;
        position: absolute;
        bottom: 20px;
        left: 20px;
      }
    }
  }

  // .slick-list {
  //   margin-right: 50px;
  //   display: grid;
  //   grid-template-columns: 1fr 1fr;
  //   grid-column-gap: 50px;
  // }

  .slick-dots {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    padding: 0;
    list-style: none;

    button {
      font-size: 0;
      border: none;
      padding: 0;
      margin: 0 6px;
      background: rgba(255, 255, 255, 0.3);
      border-radius: 50px;
      width: 63px;
      height: 3px;
    }
  }

  .slick-active button {
    background-color: $basic_white;
  }
}

