.composition {
  padding: 0 15px;

  &__title {
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    padding-bottom: 30px;
  }

  &__text {
    font-size: 18px;
    line-height: 120%;
    padding-bottom: 80px;
  }

  &__inner {
    display: grid;
    grid-row-gap: 25px;

    @media (min-width: $tablet-width) {
      grid-template-columns: 1fr 1fr;
      grid-gap: 45px;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-gap: 75px;
    }
  }

  &__item {
    max-width: 750px;
    background-color: $basic_black;
    position: relative;
    margin: 0 auto;

    &::before {
      content: "";
      position: absolute;
      // z-index: 0;
      // top: 0;
      // left: 0;
      // width: calc(100% - 2px);
      width: 100%;
      height: 100%;
      transition: all 0.2s ease-in-out 0s;
      border: 1px solid #f05c76;
    }

    &:hover::before {
      // top: -10px;
      // left: -10px;
      // right: -10px;
      // bottom: -10px;
      padding: 10px;
      transform: scale(1.08, 1.05);
      transition: all 0.2s ease-in-out 0s;
      cursor: pointer;
    }

    img {
      display: block;
    }
  }

  &__price {
    padding: 10px 0 10px 15px;
  }
}
