.page__footer {
  margin-top: 50px;
  margin-bottom: 30px;

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
  }

  &-wrapper {
    display: flex;
    flex-direction: column;
    // align-items: center;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
  }

  &-nav {
    text-align: center;
    padding: 30px 0 15px;
    margin: 0;

    @media (min-width: $tablet-width) {
      padding: 40px 0 0;
    }

    @media (min-width: $desktop-width) {
      display: flex;
      justify-content: center;
      padding: 20px 0 0;
    }

    &__item {
      padding-bottom: 15px;

      @media (min-width: $tablet-width) {
        padding-bottom: 20px;
      }

      @media (min-width: $desktop-width) {
        padding-bottom: 30px;
        margin-right: 50px;
      }
    }

    &__link {
      font-family: "Forum", cursive;
      font-weight: 400;
      font-size: 16px;
      line-height: 18px;
      color: rgba(255, 255, 255, 0.5);

      @media (min-width: $tablet-width) {
        font-size: 20px;
        line-height: 22px;
      }

      &:hover {
        color: #ffffff;
        transition: all 0.8s;
      }
    }
  }

  &-social__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 15px 0 30px;
    margin: 0;

    @media (min-width: $tablet-width) {
      padding: 20px 195px 0;
    }

    @media (min-width: $desktop-width) {
      padding: 0;
      justify-content: center;
    }
  }

  &-social__link:hover {
    opacity: 0.3;
    transition: all 0.8s;
  }

  &-social__item {
    @media (min-width: $desktop-width) {
      margin-right: 50px;
    }
  }
}
