.page {
  &__logo {
    width: 100%;
    max-height: 486px;
    object-fit: cover;
    margin-top: 15px;

    @media (min-width: $tablet-width) {
      max-height: 100%;
    }
  }

  &__body {
    background-color: $basic_violet;
    background-image: url("../img/bg-mobile-1@1x.jpg");
    background-size: 100% 466px;
    background-position: center 64px;
    background-repeat: no-repeat;
    // max-height: 466px;
    margin: 0 auto;
    min-width: 320px;

    @media (min-width: $tablet-width) {
      min-width: 768px;
      background-image: url("../img/bg-tablet-1@1x.jpg");
      background-position: center top;
      background-size: 100% 1024px;
    }

    @media (min-width: $desktop-width) {
      min-width: 1920px;
      background-image: url("../img/bg-desktop-1@1x.jpg");
      background-position: center top;
      background-size: 100% 1040px;
    }
  }

  &__composition {
    background-color: $basic_violet;
    margin: 0 auto;
    min-width: 320px;

    @media (min-width: $tablet-width) {
      min-width: 768px;
    }

    @media (min-width: $desktop-width) {
      min-width: 1920px;
    }
  }

  &__header {
    margin-bottom: 140px;
    // position: relative;

    @media (min-width: $tablet-width) {
      margin-bottom: 155px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 284px;
    }
  }

  &__header-logo {
    @media (min-width: $tablet-width) {
      width: 137px;
      height: 47px;
    }
  }

  &__header-nav {
    display: flex;
    justify-content: space-between;
    padding: 15px 0 0;

    @media (min-width: $tablet-width) {
      padding: 38px 10px 0;
    }

    @media (min-width: $desktop-width) {
      padding: 87px 0 0;
    }
  }

  &__header-button {
    display: block;
    outline: none;
    border: none;
    background-color: transparent;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      border-bottom: 1px solid #ffffff;
      border-top: 1px solid #ffffff;
      width: 37px;
      height: 10px;
      top: 26px;
      right: 30px;

      @media (min-width: $tablet-width) {
        top: 38px;
        right: 50px;
      }

      @media (min-width: $desktop-width) {
        top: 106px;
        right: 180px;
      }
    }
  }

  &__header-wrapper {
    padding: 101px 14px 0;
    // margin: 118px auto;

    @media (min-width: $tablet-width) {
      padding: 240px 14px 0;
    }

    @media (min-width: $desktop-width) {
      padding: 189px 0 0;
    }
  }

  &__header-title {
    font-family: "Playfair Display", serif;
    font-size: 30px;
    line-height: 140%;
    /* or 42px */
    text-align: center;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    color: $basic_white;

    @media (min-width: $tablet-width) {
      font-family: "Forum", cursive;
      font-size: 70px;
      line-height: 77px;
    }

    @media (min-width: $desktop-width) {
      font-size: 120px;
      line-height: 132px;
      max-width: 818px;
      margin: 0 auto;
    }
  }

  &__header-link {
    display: inline-block;
    background: rgba(0, 0, 0, 0.4);
    border: 1px solid $basic_white;
    font-family: "Forum", cursive;
    font-size: 10px;
    line-height: 11px;
    text-align: center;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    color: $basic_white;
    padding: 19px 37px 16px;
    transition: all 0.8s;
    margin-top: 35px;
    width: 100%;

    @media (min-width: $tablet-width) {
      font-size: 15px;
      line-height: 17px;
      // margin-top: 82px;
      width: 257px;
      position: relative;
      margin: 82px auto 0;
      display: block;
      padding: 22px 40px;
    }

    &:hover,
    &:focus {
      color: $basic_black;
      background: $basic_white;
      transition: all 0.8s;
    }
  }

  &__header-inner {
    display: flex;
    flex-direction: column;
  }
}

.page__header-social__list {
  display: flex;
  justify-content: space-between;
  padding: 70px 30px 0;
  margin: 0;

  @media (min-width: $tablet-width) {
    padding: 225px 127px 0;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;

    padding: 41px 0 0;
  }
}

.page__header-social__item {
  @media (min-width: $desktop-width) {
    padding-bottom: 24px;
  }
}

.page__header-social__item-link:hover {
  opacity: 0.3;
  transition: all 0.8s;
}

.page__header-arrow {
  display: none;
}

.arrow-down {
  position: relative;
  width: 14px;
  height: 21px;
  background-image: url("../img/arrow-down-mobile-1@1x.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  cursor: pointer;
  margin: 42px auto 0;

  @media (min-width: $tablet-width) {
    width: 29px;
    height: 40px;
    margin: 48px auto 0;
  }

  @media (min-width: $desktop-width) {
    margin: -90px auto 0;
  }
}

//Menu

// .page__header-menu.active {
//   left: 0;
// }

.page__header-menu {
  background-color: $basic_violet;
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  z-index: 1;
  transition: all 1.2s;

  // .active {
  //   left: 0;
  // }

  &__close {
    width: 21px;
    height: 21px;
    /* background-color: #FFFFFF; */
    position: absolute;
    top: 25px;
    right: 25px;
    cursor: pointer;

    &::after,
    &::before {
      content: "";
      width: 28px;
      height: 2px;
      background-color: #ffffff;
      position: absolute;
      bottom: 6px;
    }

    &::after {
      transform: rotate(45deg);
    }

    &::before {
      transform: rotate(-45deg);
    }
  }

  &__nav {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    overflow: hidden;
    padding: 74px 0 0;

    @media (min-width: $tablet-width) {
      padding: 148px 0 0;
    }

    @media (min-width: $desktop-width) {
      padding: 200px 0 0;
    }
  }

  &__item {
    padding-bottom: 55px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      padding-bottom: 63px;
    }

    @media (min-width: $desktop-width) {
      padding-bottom: 73px;
    }
  }

  &__link {
    font-family: "Forum", cursive;
    font-weight: 400;
    font-size: 15px;
    line-height: 17px;
    text-align: center;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    transition: all 0.8s;
    display: inline-block;

    &:hover {
      color: $basic_white;
      transition: all 0.8s;
      transform: scale(1.3);
    }

    @media (min-width: $tablet-width) {
      font-size: 30px;
      line-height: 33px;
    }

    @media (min-width: $desktop-width) {
      font-size: 35px;
      line-height: 39px;
    }
  }

  &.active {
    left: 0;
  }

  .page__header-logo {
    padding-top: 26px;
    margin: 0 auto;
    display: block;
  }
}

.page--composition {
  margin-bottom: 30px;
}
