.sales {
  margin-bottom: 50px;

  @media (min-width: $desktop-width) {
    margin-bottom: 170px;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media (min-width: $tablet-width) {
      align-items: center;
    }

    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: 410px auto;
      grid-column-gap: 164px;

      picture {
        grid-column: 2/2;
        grid-row: 1/4;
      }
    }
  }

  &__img {
    padding-bottom: 25px;
    object-fit: cover;
    width: 100%;

    @media (min-width: $tablet-width) {
      // max-width: 453px;
    }

    @media (min-width: $desktop-width) {
      max-width: 697px;
    }
  }

  &__title {
    font-size: 20px;
    line-height: 170%;
    /* or 34px */
    letter-spacing: 0.06em;
    text-transform: uppercase;
    position: relative;
    margin-right: 50px;
    margin-bottom: 20px;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      max-width: 86px;
      background-color: $basic_white;
      height: 2px;

      @media (min-width: $tablet-width) {
        margin: 0 auto;
      }

      @media (min-width: $desktop-width) {
        margin: 0;
      }
    }

    @media (min-width: $tablet-width) {
      font-size: 30px;
      margin: 0 auto 40px;
    }

    @media (min-width: $desktop-width) {
      margin: 56px 0 0;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 170%;
    /* or 24px */
    padding-bottom: 40px;

    @media (min-width: $tablet-width) {
      text-align: center;
      max-width: 453px;
      padding-bottom: 15px;
    }

    @media (min-width: $desktop-width) {
      text-align: left;
      max-width: none;
    }
  }

  &__button {
    background-color: #010001;
    padding: 18px 30px;
    font-family: "Forum", cursive;
    font-weight: 400;
    font-size: 11px;
    line-height: 12px;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    text-align: center;
    color: $basic_white;
    transition: all 0.8s;

    &:hover,
    &:focus {
      color: #000000;
      background: #ffffff;
    }

    @media (min-width: $desktop-width) {
      grid-column: 1/2;
      max-width: 245px;
      margin-bottom: 63px;
    }
  }
}
